.box-choose {
    background-color: #dfdfdf;
}

ul {
    list-style-type: none;
    padding-left: 0;
}

ul>.item-folder {
    margin-top: 15px;
    margin-bottom: 15px;
}

ul>.item-folder>button {
    border-radius: 0;
    border-color: #0033FF;
}

ul>.item-folder>button:hover {
    border-radius: 0;
    background-color: cornflowerblue;
    color: #dfdfdf;
}

ul>.item-folder>button:focus {
    border-radius: 0;
    background-color: #0033FF;
    color: #dfdfdf;
}

.textWrap {
    word-break: break-all !important;
}

.folder-selected {
    border-radius: 0;
    background-color: #0033FF;
    color: #dfdfdf;
}

.react-json-view {
    word-break: break-all;
}